"use client";

import { useState, useCallback, useEffect } from "react";

interface Alert {
  id: string;
  title: string;
  description: string;
  resourceId: string;
  resourceType: string;
  severity: string;
  status: string;
  createdAt: string;
  updatedAt: string;
}

interface AlertFilters {
  status?: string;
  severity?: string;
  resourceType?: string;
}

interface UseAlertsResult {
  alerts: Alert[];
  loading: boolean;
  error: string | null;
  filters: AlertFilters;
  setFilters: (filters: AlertFilters) => void;
  fetchAlerts: () => Promise<void>;
  dismissAlert: (id: string) => Promise<boolean>;
  resolveAlert: (id: string) => Promise<boolean>;
}

export function useAlerts(): UseAlertsResult {
  const [alerts, setAlerts] = useState<Alert[]>([]);
  const [loading, setLoading] = useState<boolean>(false);
  const [error, setError] = useState<string | null>(null);
  const [filters, setFilters] = useState<AlertFilters>({});

  const fetchAlerts = useCallback(async () => {
    try {
      setLoading(true);
      setError(null);
      
      let url = "/api/alerts";
      const params = new URLSearchParams();
      
      if (filters.status) {
        params.append("status", filters.status);
      }
      if (filters.severity) {
        params.append("severity", filters.severity);
      }
      if (filters.resourceType) {
        params.append("resourceType", filters.resourceType);
      }
      
      if (params.toString()) {
        url += `?${params.toString()}`;
      }
      
      const response = await fetch(url);
      
      if (!response.ok) {
        const { error } = await response.json();
        throw new Error(error || "Erro ao buscar alertas");
      }
      
      const data = await response.json();
      setAlerts(data.alerts || []);
    } catch (err) {
      setError(err instanceof Error ? err.message : "Erro ao buscar alertas");
    } finally {
      setLoading(false);
    }
  }, [filters]);

  const updateAlertStatus = useCallback(async (id: string, action: "dismiss" | "resolve") => {
    try {
      setLoading(true);
      setError(null);
      
      const response = await fetch("/api/alerts", {
        method: "PATCH",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          id,
          action,
        }),
      });
      
      if (!response.ok) {
        const { error } = await response.json();
        throw new Error(error || `Erro ao ${action === "dismiss" ? "arquivar" : "resolver"} alerta`);
      }
      
      // Atualizar lista de alertas
      await fetchAlerts();
      return true;
    } catch (err) {
      setError(err instanceof Error ? err.message : `Erro ao ${action === "dismiss" ? "arquivar" : "resolver"} alerta`);
      return false;
    } finally {
      setLoading(false);
    }
  }, [fetchAlerts]);

  const dismissAlert = useCallback((id: string) => {
    return updateAlertStatus(id, "dismiss");
  }, [updateAlertStatus]);

  const resolveAlert = useCallback((id: string) => {
    return updateAlertStatus(id, "resolve");
  }, [updateAlertStatus]);

  // Buscar alertas ao montar o componente ou quando os filtros mudarem
  useEffect(() => {
    fetchAlerts();
  }, [fetchAlerts]);

  return {
    alerts,
    loading,
    error,
    filters,
    setFilters,
    fetchAlerts,
    dismissAlert,
    resolveAlert,
  };
} 