"use client";

import { useAlerts } from "@/hooks/useAlerts";
import { motion } from "framer-motion";
import { useState } from "react";
import { 
  Bell, AlertTriangle, AlertCircle, Info, 
  CheckCircle, XCircle, Archive, Shield, 
  Search, Filter, RefreshCw, Trash2 
} from "lucide-react";

export default function AlertsPage() {
  const { 
    alerts, 
    loading, 
    error, 
    filters, 
    setFilters, 
    dismissAlert, 
    resolveAlert,
    fetchAlerts
  } = useAlerts();
  
  const [cleanupLoading, setCleanupLoading] = useState(false);
  const [cleanupMessage, setCleanupMessage] = useState<string | null>(null);
  const [showFilters, setShowFilters] = useState(false);
  const [actionLoading, setActionLoading] = useState<string | null>(null);

  const handleDismiss = async (id: string) => {
    setActionLoading(id);
    await dismissAlert(id);
    setActionLoading(null);
  };

  const handleResolve = async (id: string) => {
    setActionLoading(id);
    await resolveAlert(id);
    setActionLoading(null);
  };

  const handleRefresh = async () => {
    await fetchAlerts();
  };

  const handleCleanupDuplicates = async () => {
    if (window.confirm("Deseja remover todos os alertas duplicados? Esta ação não pode ser desfeita.")) {
      try {
        setCleanupLoading(true);
        setCleanupMessage(null);
        
        const response = await fetch('/api/alerts/cleanup', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json'
          }
        });
        
        if (!response.ok) {
          const data = await response.json();
          throw new Error(data.error || 'Erro ao limpar alertas duplicados');
        }
        
        const result = await response.json();
        setCleanupMessage(result.message);
        
        // Recarregar alertas em vez de recarregar a página inteira
        await fetchAlerts();
      } catch (error) {
        setCleanupMessage(`Erro: ${error instanceof Error ? error.message : 'Erro desconhecido'}`);
      } finally {
        setCleanupLoading(false);
      }
    }
  };

  const getSeverityIcon = (severity: string) => {
    switch (severity) {
      case 'CRITICAL':
        return <AlertCircle className="h-5 w-5 text-red-500" />;
      case 'HIGH':
        return <AlertTriangle className="h-5 w-5 text-orange-500" />;
      case 'MEDIUM':
        return <AlertTriangle className="h-5 w-5 text-yellow-500" />;
      case 'LOW':
        return <Info className="h-5 w-5 text-blue-500" />;
      default:
        return <Info className="h-5 w-5 text-gray-500" />;
    }
  };

  const getSeverityText = (severity: string) => {
    switch (severity) {
      case 'CRITICAL': return 'Crítica';
      case 'HIGH': return 'Alta';
      case 'MEDIUM': return 'Média';
      case 'LOW': return 'Baixa';
      default: return 'Desconhecida';
    }
  };

  const getSeverityColor = (severity: string) => {
    switch (severity) {
      case 'CRITICAL': return 'bg-red-50 text-red-800 border-red-200';
      case 'HIGH': return 'bg-orange-50 text-orange-800 border-orange-200';
      case 'MEDIUM': return 'bg-yellow-50 text-yellow-800 border-yellow-200';
      case 'LOW': return 'bg-blue-50 text-blue-800 border-blue-200';
      default: return 'bg-gray-50 text-gray-800 border-gray-200';
    }
  };

  const getStatusColor = (status: string) => {
    switch (status) {
      case 'active': return 'bg-red-100 text-red-800';
      case 'dismissed': return 'bg-gray-100 text-gray-800';
      case 'resolved': return 'bg-green-100 text-green-800';
      default: return 'bg-gray-100 text-gray-800';
    }
  };

  const getStatusText = (status: string) => {
    switch (status) {
      case 'active': return 'Ativo';
      case 'dismissed': return 'Arquivado';
      case 'resolved': return 'Resolvido';
      default: return 'Desconhecido';
    }
  };

  const getStatusIcon = (status: string) => {
    switch (status) {
      case 'active': return <Bell className="h-4 w-4" />;
      case 'dismissed': return <Archive className="h-4 w-4" />;
      case 'resolved': return <CheckCircle className="h-4 w-4" />;
      default: return <Info className="h-4 w-4" />;
    }
  };

  // Estatísticas
  const activeCount = alerts.filter(a => a.status === 'active').length;
  const criticalCount = alerts.filter(a => a.severity === 'CRITICAL' && a.status === 'active').length;
  const highCount = alerts.filter(a => a.severity === 'HIGH' && a.status === 'active').length;

  return (
    <div>
      <motion.div
        className="mb-6"
        initial={{ opacity: 0, y: -20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.3 }}
      >
        <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center gap-4">
          <div>
            <h1 className="text-3xl font-bold text-gray-900 flex items-center">
              <Bell className="mr-2 h-6 w-6 text-indigo-600" />
              Alertas de Segurança
            </h1>
            <p className="mt-2 text-gray-600">
              Monitore e gerencie alertas de segurança dos seus recursos AWS.
            </p>
          </div>
          <div className="flex gap-2 self-end sm:self-auto">
            <button
              onClick={() => setShowFilters(!showFilters)}
              className="p-2 bg-white text-gray-700 rounded-md border border-gray-300 hover:bg-gray-50 transition-colors"
              title="Filtros"
            >
              <Filter className="h-5 w-5" />
            </button>
            <button
              onClick={handleRefresh}
              disabled={loading}
              className="p-2 bg-white text-gray-700 rounded-md border border-gray-300 hover:bg-gray-50 transition-colors disabled:opacity-50"
              title="Atualizar"
            >
              <RefreshCw className={`h-5 w-5 ${loading ? 'animate-spin' : ''}`} />
            </button>
            <button
              onClick={handleCleanupDuplicates}
              disabled={cleanupLoading}
              className="p-2 bg-white text-indigo-700 rounded-md border border-indigo-300 hover:bg-indigo-50 transition-colors disabled:opacity-50"
              title="Remover alertas duplicados"
            >
              <Trash2 className="h-5 w-5" />
            </button>
          </div>
        </div>
        
        {cleanupMessage && (
          <motion.div 
            initial={{ opacity: 0, y: -10 }}
            animate={{ opacity: 1, y: 0 }}
            className="mt-4 p-4 bg-green-50 text-green-800 rounded-md border border-green-200 flex items-start"
          >
            <CheckCircle className="h-5 w-5 mr-2 flex-shrink-0 mt-0.5" />
            <div>{cleanupMessage}</div>
          </motion.div>
        )}

        {/* Estatísticas */}
        <div className="mt-6 grid grid-cols-1 md:grid-cols-3 gap-4">
          <motion.div 
            initial={{ opacity: 0, y: 10 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ delay: 0.1 }}
            className="bg-white shadow-sm rounded-lg p-4 border-l-4 border-indigo-500"
          >
            <div className="flex justify-between items-center">
              <div>
                <p className="text-sm text-gray-500">Total de Alertas Ativos</p>
                <p className="text-2xl font-bold text-gray-900">{activeCount}</p>
              </div>
              <Bell className="h-8 w-8 text-indigo-500 bg-indigo-100 p-1.5 rounded-full" />
            </div>
          </motion.div>
          
          <motion.div 
            initial={{ opacity: 0, y: 10 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ delay: 0.2 }}
            className="bg-white shadow-sm rounded-lg p-4 border-l-4 border-red-500"
          >
            <div className="flex justify-between items-center">
              <div>
                <p className="text-sm text-gray-500">Alertas Críticos</p>
                <p className="text-2xl font-bold text-gray-900">{criticalCount}</p>
              </div>
              <AlertCircle className="h-8 w-8 text-red-500 bg-red-100 p-1.5 rounded-full" />
            </div>
          </motion.div>
          
          <motion.div 
            initial={{ opacity: 0, y: 10 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ delay: 0.3 }}
            className="bg-white shadow-sm rounded-lg p-4 border-l-4 border-orange-500"
          >
            <div className="flex justify-between items-center">
              <div>
                <p className="text-sm text-gray-500">Alertas de Alta Severidade</p>
                <p className="text-2xl font-bold text-gray-900">{highCount}</p>
              </div>
              <AlertTriangle className="h-8 w-8 text-orange-500 bg-orange-100 p-1.5 rounded-full" />
            </div>
          </motion.div>
        </div>
      </motion.div>

      {/* Filtros */}
      {showFilters && (
        <motion.div 
          initial={{ opacity: 0, height: 0 }}
          animate={{ opacity: 1, height: 'auto' }}
          exit={{ opacity: 0, height: 0 }}
          className="bg-white rounded-lg shadow-md mb-6 overflow-hidden"
        >
          <div className="p-4 border-b border-gray-200 bg-gray-50">
            <h2 className="text-lg font-medium flex items-center">
              <Filter className="mr-2 h-5 w-5 text-indigo-600" />
              Filtros
            </h2>
          </div>
          <div className="p-4">
            <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
              <div>
                <label htmlFor="statusFilter" className="block text-sm font-medium text-gray-700 mb-1">
                  Status
                </label>
                <select
                  id="statusFilter"
                  className="block w-full rounded-md border-gray-300 shadow-sm focus:border-indigo-500 focus:ring-indigo-500 sm:text-sm"
                  value={filters.status || ''}
                  onChange={(e) => setFilters({ ...filters, status: e.target.value || undefined })}
                >
                  <option value="">Todos</option>
                  <option value="active">Ativos</option>
                  <option value="dismissed">Arquivados</option>
                  <option value="resolved">Resolvidos</option>
                </select>
              </div>
              
              <div>
                <label htmlFor="severityFilter" className="block text-sm font-medium text-gray-700 mb-1">
                  Severidade
                </label>
                <select
                  id="severityFilter"
                  className="block w-full rounded-md border-gray-300 shadow-sm focus:border-indigo-500 focus:ring-indigo-500 sm:text-sm"
                  value={filters.severity || ''}
                  onChange={(e) => setFilters({ ...filters, severity: e.target.value || undefined })}
                >
                  <option value="">Todas</option>
                  <option value="CRITICAL">Crítica</option>
                  <option value="HIGH">Alta</option>
                  <option value="MEDIUM">Média</option>
                  <option value="LOW">Baixa</option>
                </select>
              </div>
              
              <div>
                <label htmlFor="resourceFilter" className="block text-sm font-medium text-gray-700 mb-1">
                  Tipo de Recurso
                </label>
                <select
                  id="resourceFilter"
                  className="block w-full rounded-md border-gray-300 shadow-sm focus:border-indigo-500 focus:ring-indigo-500 sm:text-sm"
                  value={filters.resourceType || ''}
                  onChange={(e) => setFilters({ ...filters, resourceType: e.target.value || undefined })}
                >
                  <option value="">Todos</option>
                  <option value="EC2">EC2</option>
                  <option value="S3">S3</option>
                  <option value="RDS">RDS</option>
                  <option value="IAM">IAM</option>
                </select>
              </div>
            </div>
          </div>
        </motion.div>
      )}
      
      {/* Conteúdo principal */}
      {error && (
        <motion.div
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          className="bg-red-50 border border-red-200 text-red-800 px-4 py-3 rounded-md flex items-start gap-2 mb-6"
        >
          <AlertTriangle className="h-5 w-5 text-red-500 flex-shrink-0 mt-0.5" />
          <div>{error}</div>
        </motion.div>
      )}
      
      {loading ? (
        <div className="flex flex-col items-center justify-center py-12 bg-white rounded-lg shadow-md">
          <RefreshCw className="h-8 w-8 text-indigo-500 animate-spin mb-4" />
          <p className="text-gray-500">Carregando alertas...</p>
        </div>
      ) : alerts.length === 0 ? (
        <motion.div 
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          className="bg-white rounded-lg shadow-md p-12 text-center"
        >
          <Bell className="h-12 w-12 text-gray-400 mx-auto mb-4" />
          <h3 className="text-lg font-medium text-gray-900 mb-2">Nenhum alerta encontrado</h3>
          <p className="text-gray-500">Não há alertas correspondentes aos filtros selecionados.</p>
        </motion.div>
      ) : (
        <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
          {alerts.map((alert) => (
            <motion.div
              key={alert.id}
              initial={{ opacity: 0, y: 10 }}
              animate={{ opacity: 1, y: 0 }}
              className={`rounded-lg shadow-sm border ${getSeverityColor(alert.severity)}`}
            >
              <div className="p-4">
                <div className="flex justify-between items-start mb-3">
                  <div className="flex items-center">
                    {getSeverityIcon(alert.severity)}
                    <span className="ml-2 font-medium">
                      {getSeverityText(alert.severity)}
                    </span>
                  </div>
                  <div className={`px-2 py-1 rounded-full text-xs font-medium flex items-center ${getStatusColor(alert.status)}`}>
                    {getStatusIcon(alert.status)}
                    <span className="ml-1">{getStatusText(alert.status)}</span>
                  </div>
                </div>
                
                <h3 className="text-lg font-semibold mb-2">{alert.title}</h3>
                <p className="text-gray-700 text-sm mb-4 line-clamp-2">{alert.description}</p>
                
                <div className="flex justify-between items-center">
                  <div>
                    <div className="text-xs text-gray-500">Recurso</div>
                    <div className="flex items-center mt-1">
                      <Shield className="h-4 w-4 text-gray-400 mr-1" />
                      <div className="text-sm">
                        <span className="font-medium">{alert.resourceType}</span>
                        <span className="mx-1">·</span>
                        <span className="text-gray-600">{alert.resourceId}</span>
                      </div>
                    </div>
                  </div>
                  <div className="text-right">
                    <div className="text-xs text-gray-500">Data</div>
                    <div className="text-sm">
                      {new Date(alert.createdAt).toLocaleDateString()}
                    </div>
                  </div>
                </div>
                
                {alert.status === 'active' && (
                  <div className="mt-4 pt-3 border-t border-gray-100 flex justify-end gap-2">
                    <button
                      onClick={() => handleDismiss(alert.id)}
                      disabled={actionLoading === alert.id}
                      className="px-3 py-1.5 text-xs font-medium bg-gray-100 text-gray-800 rounded hover:bg-gray-200 transition-colors disabled:opacity-50 flex items-center"
                    >
                      <Archive className="h-3.5 w-3.5 mr-1" />
                      Arquivar
                    </button>
                    <button
                      onClick={() => handleResolve(alert.id)}
                      disabled={actionLoading === alert.id}
                      className="px-3 py-1.5 text-xs font-medium bg-green-100 text-green-800 rounded hover:bg-green-200 transition-colors disabled:opacity-50 flex items-center"
                    >
                      <CheckCircle className="h-3.5 w-3.5 mr-1" />
                      Resolver
                    </button>
                  </div>
                )}
              </div>
            </motion.div>
          ))}
        </div>
      )}
    </div>
  );
} 